import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, FormGroup, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { CLASS_NAME, CodigoReceitaDetalhamento } from '../../../../models/dtos/mlf/codigoReceitaDetalhamento';

interface CodigoReceitaFinanceiraEditViewProps extends EditViewProps<CodigoReceitaDetalhamento> {}

const CodigoReceitaFinanceiraDetalhamentoEditView: FC<CodigoReceitaFinanceiraEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<CodigoReceitaDetalhamento>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  return (
    <WindowComponent title="Código de Detalhamento de Receita de Instituições Financeiras" wikiId={2378}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 8, required: true } }}
              id="codigo"
              isPrependLabel={true}
              label="Código"
              labelWidth={defaultWidth}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 255, required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
            />
          </Col>
        </Row>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput
              id="checkInstituicaoFinanceira"
              innerRef={formProps.register}
              label="Instituição Financeira"
              name="instituicaoFinanceira"
              type="switch"
            />
          </Col>
        </FormGroup>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput id="checkSegurosPrivados" innerRef={formProps.register} label="Seguros Privados" name="segurosPrivados" type="switch" />
          </Col>
        </FormGroup>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput
              id="checkPrevidenciaComplementar"
              innerRef={formProps.register}
              label="Previdência Complementar"
              name="previdenciaComplementar"
              type="switch"
            />
          </Col>
        </FormGroup>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput id="checkCapitalizacao" innerRef={formProps.register} label="Capitalização" name="capitalizacao" type="switch" />
          </Col>
        </FormGroup>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput
              label="Assistência de Saúde"
              id="checkAssistenciaSaude"
              innerRef={formProps.register}
              type="switch"
              name="assistenciaSaude"
            />
          </Col>
        </FormGroup>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput
              checked={true}
              disabled={true}
              label="Múltiplos"
              id="checkMultiplos"
              innerRef={formProps.register}
              name="multiplos"
              type="switch"
            />
          </Col>
        </FormGroup>
      </EditForm>
    </WindowComponent>
  );
};

export default CodigoReceitaFinanceiraDetalhamentoEditView;
